	module.exports = {"data":{"post":{"html":"<p>In commemoration of the events that occurred <a href=\"http://www.papascott.de/archives/2002/09/09/young-dumb-and-excited/\">25 years ago tomorrow</a> (for which all photographic evidence seems to have disappeared, much to the chagrin of the participants), the weblog PapaScott will remain silent today and for the weekend. All three members of the Petersen-Hanson family are away observing the anniversary \"in the near abroad, within our traditional sphere of influence\", as Vladimir Putin would probably say. One could also say that they have disappeared from the face of the earth for a couple of days, but will return home on Sunday evening to fulfill their respective obligations. We kindly request your understanding and respect.</p>\n<p>We hereby submit the following documentation. Click to enlarge.</p>\n<p><a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/09/marriagelicense.jpg\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/09/marriagelicense-300x125.jpg\" alt=\"\" title=\"marriagelicense\" width=\"300\" height=\"125\" class=\"alignnone size-medium wp-image-3275\" /></a></p>\n<p><a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/09/marriagelicense2.jpg\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/09/marriagelicense2-300x225.jpg\" alt=\"\" title=\"marriagelicense2\" width=\"300\" height=\"225\" class=\"alignnone size-medium wp-image-3278\" /></a></p>","fields":{"title":"Moment of Silence","date":"05 Sep 2008","url":"https://papascott.de/2008-09-05-moment-of-silence/"}}},"pathContext":{"slug":"/2008-09-05-moment-of-silence/","prev":{"fields":{"title":"Right of Way","date":"29 Aug 2008","slug":"/2008-08-29-right-of-way/"}},"next":{"fields":{"title":"Oslo Cruise","date":"08 Sep 2008","slug":"/2008-09-08-oslo-cruise/"}}}}

